@import url('https://fonts.googleapis.com/css2?family=Comic+Neue:wght@300&display=swap');


*{
  margin: 0;
  padding: 0;
  background-color: rgb(26, 26, 26);
}

.header-style{
  width: 100vw;
  height: 10vh;
  color: rgb(0, 174, 255);
  font-size: 45px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.Calc-main{
  padding: 10px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.box{
  background-color: rgb(246, 216, 23);
  border: 1px solid grey;
  border-radius: 20px;
  margin: 4px 4px;
  cursor: pointer;
  font-size: 20px;
  height: 70px;
  width: 100px;
  box-shadow: 1px 1px 5px rgb(241, 204, 83);
  font-family: 'Comic Neue', cursive;
  font-weight: 1000;
}

.box:hover{
  background-color: rgb(192, 167, 11);
}

.Key-rowpad{
  display: flex;
  align-items: center;
  justify-content: center;
}

.Calc-display{
  width: 400px;
  height: 80px;
}

.Calc-screen, .Calc-screen-answer{
  height: 40px;
  color: white;
  display: flex;
  align-items: center;
  justify-content: end;
  padding: 0 20px;
  font-size: 30px;
}

.Calc-equal{
  background-color: rgb(81, 255, 0);
}

.Calc-equal:hover{
  background-color: rgb(166, 255, 0);
}